.pdf-container {
  display: flex;
  flex: 1; /* Takes up the remaining space */
  justify-content: center; /* Horizontally center the pages */
  align-items: flex-start; /* Align pages to the top */
  overflow-y: scroll; /* Enable vertical scrolling */
  /*height: 96vh;*/ /* Full viewport height */
  background-color: #f9f9f9; /* Optional: Background color */
  /*padding: 10px;*/
}

.pdf-document {
  /*position: relative;*/
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pdf-page {
  /*position: relative;*/
  /*margin: 0 auto;*/
  margin-bottom: 10px; /* Add space between pages */
  border-bottom: 1px solid #ddd;
}

.react-highlight-container {
  position: relative;
}

.react-highlight-container:hover {
  border: 4px solid #28e08a;
}

.floating-panel {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 200px;
  padding: 10px;
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  z-index: 1000;
}

