.Toolbar {
  display: flex;
  flex-shrink: 0; /* Prevents the navbar from shrinking */
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 50px;
  background-color: #333;
  color: #ffffff;
  /*border-bottom: 1px solid #9096a2;*/
  height: 40px;
}

.ZoomControls {
  display: flex;
  align-items: center;
}

.ZoomControls button {
  background: none;
  /* Remove background */
  /*color: #f5f5f5;*/
  border: none;
  /* Remove border */
  cursor: pointer;
  font-size: 24px;
  padding: 2px;
  margin-right: 8px;
  transition: color 0.3s, transform 0.2s;
}

.ZoomControls button:hover {
  color: #b958ff;
  transform: scale(1.05);
}

.HighlightButton {
  background: none;
  /* Remove background */
  /*color: #f5f5f5;*/
  border: none;
  /* Remove border */
  cursor: pointer;
  font-size: 16px;
  padding: 2px;
  margin-right: 8px;
  transition: color 0.3s, transform 0.2s;
}

.HighlightButton:hover {
  color: #b958ff;
  transform: scale(1.05);
}

.HighlightButton.active {
  color: #b958ff;
}